<template>
    <section class="control">
        <el-table :data="tableData" class="table" stripe border>
            <el-table-column
                prop="gate"
                label="闸口">
            </el-table-column>
            <el-table-column
                prop="channel"
                label="通道">
            </el-table-column>
            <el-table-column
                prop="ipc"
                label="工控机">
                <template scope ="scope">
                    <el-button type="info" size="small" @click ="reset(scope.row.gate,scope.row.channel, 'IPC')">重启</el-button>
                </template>
            </el-table-column>
            <el-table-column
                prop="plc"
                label="PLC">
                <template scope ="scope">
                    <el-button type="info" size="small" @click ="reset(scope.row.gate,scope.row.channel, 'PLC')">重启</el-button>
                </template>
            </el-table-column>
        </el-table>
    </section>
</template>

<script>
    import api from "@/api/index"
    export default {
        data () {
            return {
                token:localStorage.getItem("token"),
                value:true,
                tableData: [
                    {gate: 'B', channel: 1, ipc: false, plc: false},
                    {gate: 'B', channel: 2, ipc: false, plc: false},
                    {gate: 'B', channel: 3, ipc: false, plc: false},
                    {gate: 'B', channel: 4, ipc: false, plc: false},
                    {gate: 'B', channel: 5, ipc: false, plc: false},
                    {gate: 'B', channel: 6, ipc: false, plc: false},
                    {gate: 'B', channel: 7, ipc: false, plc: false},
                    {gate: 'B', channel: 8, ipc: false, plc: false},
                    {gate: 'B', channel: 9, ipc: false, plc: false},
                    {gate: 'B', channel: 10, ipc: false, plc: false},
                    {gate: 'B', channel: 11, ipc: false, plc: false},
                    {gate: 'B', channel: 12, ipc: false, plc: false},
                    {gate: 'B', channel: 13, ipc: false, plc: false},
                    {gate: 'B', channel: 14, ipc: false, plc: false},
                    {gate: 'B', channel: 15, ipc: false, plc: false},
                    {gate: 'B', channel: 17, ipc: false, plc: false},
                    {gate: 'B', channel: 18, ipc: false, plc: false},
                    {gate: 'B', channel: 19, ipc: false, plc: false},
                    {gate: 'B', channel: 20, ipc: false, plc: false},
                ]
            }
        },
        methods: {
           async reset(gate, channel, type){
               await this.$confirm('确认重启么?', '重启', {
                   type: 'warning'
               })

               api.controlDevice({
                    token:this.token,
                    gate_id:gate,
                    chnl_no: channel,
                    type
                }).then((res) =>{
                    if(type ==="IPC"){
                        type = "工控机"
                    }
                    this.$message({
                        message:`${type} 闸口${gate} 通道${channel} 重启成功`,
                        type:"success"
                    })
                }).catch((err) =>{
                        if(type ==="IPC"){
                            type = "工控机"
                        }
                        this.$message({
                            message:`${type} 闸口${gate} 通道${channel} 重启失败`,
                            type:"error"
                        })
                    }
                )

            }
        },
        mounted () {
        }
    }
</script>

<style scoped lang="scss">
    .control {
        width: 80%;
        .table {
            width: 100%;
            margin-top: 10px;
        }
    }
</style>



